import React, {Component} from 'react';
import './App.css';
import {observer} from "mobx-react";
import UserStores from "./stores/UserStores";
import {Header} from "./components/LayoutComponents/Header";
import Navigation from "./components/LayoutComponents/Navigation";
import Sidebar from "./components/LayoutComponents/Sidebar";
import {BrowserRouter, Route, Switch,} from "react-router-dom";
import {Footer} from "./components/LayoutComponents/Footer";
import GeneratePdf from "./components/PageComponents/EditableLayer/GeneratePdf";
import {AccessibleMobile} from "./components/PageComponents/items/AccessibleMobile";
import {AccesibleDocuments} from "./components/PageComponents/items/AccesibleDocuments";
import {AccessibleWeb} from "./components/PageComponents/items/AccessibleWeb";
import {AccessibleKiosk} from "./components/PageComponents/items/AccessibleKiosk";
import Login from "./components/PageComponents/Login";
import {LawsAndRegulations} from "./components/PageComponents/LawsAndRegulations";
import {OnlineCourses} from "./components/PageComponents/OnlineCourses";
import {Home} from "./components/PageComponents/Home";
import {AboutUs} from "./components/PageComponents/AboutUs";
import {EditorPages} from "./components/PageComponents/Editor/EditorPages";
import {EditorNewPage} from "./components/PageComponents/Editor/EditorNewPage";
import PdfViewer from "./components/PageComponents/EditableLayer/PdfViewer";

let path = null;

class App extends Component {


    async componentDidMount() {
        path = window.location.pathname;


        try {
            let res = await fetch('/isLoggedIn', {
                method: 'post',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                }
            });
            let result = await res.json();

            if (result && result.success) {
                UserStores.loading = false;
                UserStores.isLoggedIn = true;
                UserStores.username = result.username;
                localStorage.setItem('user', JSON.stringify(UserStores));
            } else {
                localStorage.removeItem('user');
                console.log("Ikke logget in");

            }
        } catch (e) {
            localStorage.removeItem('user');
            console.log(e);
        }
    }

    render() {

        return (<BrowserRouter>
                <Header />
                <Navigation parentMethod={this.doLogout} user={UserStores}/>
                <div className="container-fluid h-auto">

                    {
                        window.location.pathname === '/' || window.location.pathname === '/aboutus' || window.location.pathname === '/login' ? (
                            <div className="row content h-auto">
                                <div className="col-md-12 ">
                                    <Switch>
                                        <Route exact path="/" render={(props) => <Home {...props} user={UserStores}/>}/>
                                        <Route path="/onlinecourses"
                                               render={(props) => <OnlineCourses {...props} user={UserStores}/>}/>
                                        <Route path="/lawsAndRegulations"
                                               render={(props) => <LawsAndRegulations {...props} user={UserStores}/>}/>
                                        <Route path="/login" render={(props) => <Login {...props} user={UserStores}/>}/>
                                        <Route path="/accessiblekiosk"
                                               render={(props) => <AccessibleKiosk {...props} user={UserStores}/>}/>
                                        <Route path="/accessibledocuments"
                                               render={(props) => <AccesibleDocuments {...props} user={UserStores}/>}/>
                                        <Route path="/accessibleweb"
                                               render={(props) => <AccessibleWeb {...props} user={UserStores}/>}/>
                                        <Route path="/accessiblemobile"
                                               render={(props) => <AccessibleMobile {...props} user={UserStores}/>}/>
                                        <Route path="/aboutus"
                                               render={(props) => <AboutUs {...props} user={UserStores}/>}/>
                                        <Route path="/pdf/:id" component={PdfViewer}/>
                                        <Route path="/addPdf/:id" component={GeneratePdf}/>

                                    </Switch>
                                </div>
                            </div>
                        ) : (
                            <div className="row content h-auto">
                                <Sidebar/>
                                <div className="col-md-10 mt-3 order-1">
                                    <Switch>
                                        <Route exact path="/" render={(props) => <Home {...props} user={UserStores}/>}/>

                                        //Main routers
                                        <Route exact path="/onlinecourses/"
                                               render={(props) => <OnlineCourses {...props} user={UserStores}/>}/>
                                        <Route exact path="/lawsAndRegulations/"
                                               render={(props) => <LawsAndRegulations {...props} user={UserStores}/>}/>
                                        <Route exact path="/accessiblekiosk/"
                                               render={(props) => <AccessibleKiosk {...props} user={UserStores}/>}/>
                                        <Route exact path="/accessibledocuments/"
                                               render={(props) => <AccesibleDocuments {...props} user={UserStores}/>}/>
                                        <Route exact path="/accessibleweb/"
                                               render={(props) => <AccessibleWeb {...props} user={UserStores}/>}/>
                                        <Route exact path="/accessiblemobile/"
                                               render={(props) => <AccessibleMobile {...props} user={UserStores}/>}/>
                                        <Route path="/login" render={(props) => <Login {...props} user={UserStores}/>}/>
                                        <Route path="/aboutus"
                                               render={(props) => <AboutUs {...props} user={UserStores}/>}/>

                                        <Route path="/:id/page/:docId/" component={EditorPages}/>
                                        <Route exact path="/:id/newPage/"  component={EditorNewPage}/>
                                        <Route path="/:id/pdf/:id/" component={PdfViewer}/>
                                        <Route path="/:id/addPdf/" component={GeneratePdf}/>

                                    </Switch>
                                </div>
                            </div>
                        )
                    }

                </div>
                <br/><br/><br/>
                <Footer/>
            </BrowserRouter>
        );
    }

}


export default observer(App);
