import React, {useState} from "react";
import '../../App.css';
import {PdfList} from "../PageComponents/EditableLayer/PdfList";
import Collapse from "react-bootstrap/Collapse";
import {OtherFilesList} from "../PageComponents/EditableLayer/OtherFilesList";
import {PageList} from "../PageComponents/EditableLayer/PageList";

function PdfFiles() {
    const [open, setOpen] = useState(false);
    let path = window.location.pathname;
    let res = path.split("/");
    return (
        <>
            <div className="card-header bg-info text-white text-uppercase">
                <i className="fa fa-list "></i> Pdf files

                <i className=' fa fa-angle-down float-right  fa-2x' onClick={() => setOpen(!open)}
                   aria-controls="example-collapse-text"
                   aria-expanded={open}></i>
            </div>

            <Collapse in={open}>
                <div id="example-collapse-text">
                    <PdfList category={res[1]}/>
                </div>
            </Collapse>
        </>
    );
}

function OtherFiles() {
    const [open, setOpen] = useState(false);
    let path = window.location.pathname;
    let res = path.split("/");
    return (
        <>
            <div className="card-header bg-info text-white text-uppercase">
                <i className="fa fa-list "></i> Other files

                <i className=' fa fa-angle-down float-right  fa-2x' onClick={() => setOpen(!open)}
                   aria-controls="example-collapse-text"
                   aria-expanded={open}></i>
            </div>

            <Collapse in={open}>
                <div id="example-collapse-text">
                    <OtherFilesList category={res[1]}/>
                </div>
            </Collapse>
        </>
    );
}

function PageListFunc() {
    const [open, setOpen] = useState(false);
    let path = window.location.pathname;
    let res = path.split("/");
    return (
        <>
            <div className="card-header bg-info text-white text-uppercase">
                <i className="fa fa-list "></i> Pages

                <i className=' fa fa-angle-down float-right  fa-2x' onClick={() => setOpen(!open)}
                   aria-controls="example-collapse-text"
                   aria-expanded={open}></i>
            </div>

            <Collapse in={open}>
                <div id="example-collapse-text">
                    <PageList category={res[1]} name='Laws And Regulations'/>
                </div>
            </Collapse>
        </>
    );
}


export default class Sidebar extends React.Component {
    constructor(props) {
        super(props);
    }


    render() {
        return (<div className="col-md-2 sidebar  mt-3 order-md-1 order-2 pr-md-0 ">
                <div className="card mb-3 shadow rounded bg-light pt-0 pb-0 d-none d-xl-block">

                    <div className="card-header bg-info text-white text-uppercase"><i
                        className="fa fa-list"></i> Categories

                    </div>
                    <ul className="list-group ">

                        <li className="list-group-item list-group-item-info text-dark "> Learning material</li>

                        <li className="list-group-item"><a href="/accessiblekiosk"
                                                           className=' text-dark stretched-link list-group-item-action '> Accessible
                            Kiosk </a></li>
                        <li className="list-group-item"><a href="/accessibleweb"
                                                           className=' text-dark stretched-link list-group-item-action '> Accessible
                            Web </a></li>
                        <li className="list-group-item"><a href="/accessibledocuments"
                                                           className=' text-dark stretched-link list-group-item-action '> Accessible
                            Documents </a></li>
                        <li className="list-group-item"><a href="/accessiblemobile"
                                                           className=' text-dark stretched-link list-group-item-action '> Accessible
                            Mobile </a></li>
                        <li className="list-group-item list-group-item-info text-dark"> Other</li>

                        <li className="list-group-item"><a href="/lawsandregulations"
                                                           className=' text-dark stretched-link list-group-item-action '> Laws
                            and regulations </a></li>
                        <li className="list-group-item"><a href="/onlinecourses"
                                                           className=' text-dark stretched-link list-group-item-action '> Online
                            courses </a></li>

                    </ul>

                </div>
                <div className="card mb-3 shadow rounded bg-light pt-0 pb-0 ">
                    <PdfFiles/>
                </div>
            <div className="card mb-3 shadow rounded bg-light pt-0 pb-0 ">
                <OtherFiles/>
            </div>
                <div className="card mb-3 shadow rounded bg-light pt-0 pb-0 ">
                    <PageListFunc/>
                </div>
            </div>

        )
    }
}