import React, {Fragment, useState} from "react";

import "bootstrap/dist/css/bootstrap.css";
import Button from "react-bootstrap/Button";
import jsPDF from "jspdf";

const GeneratePdf = () => {

        async function paste(index) {
            const text = await navigator.clipboard.readText();
            const values = [...inputFields];
            if (values[index].title != null) {
                values[index].title = text;
            } else {
                values[index].text = text;
            }
            setInputFields(values);
        }

        const [inputFields, setInputFields] = useState([
            {title: ''}
        ]);
        const [filename, setFilename] = useState('');

        const handleAddFieldsTitle = () => {
            const values = [...inputFields];
            values.push({title: ''});
            setInputFields(values);
        };

        const handleAddFieldsText = () => {
            const values = [...inputFields];
            values.push({text: ''});
            setInputFields(values);
        };

        const handleRemoveFields = index => {
            const values = [...inputFields];
            values.splice(index, 1);
            setInputFields(values);
        };

        const handleInputChange = (index, event) => {
            const values = [...inputFields];
            if (event.target.name === "title") {
                values[index].title = event.target.value;
            } else {
                values[index].text = event.target.value;
            }

            setInputFields(values);
        };
        const handleFilenameChange = (event) => {
            setFilename(event.target.value);
        }


        const handleSubmit = e => {
            e.preventDefault();

        };

        const toPdf = () => {
            let doc = new jsPDF();
            doc.fromHTML(document.getElementById('pdf'), 15,
                0.5,
                {
                    'width': 180
                });
            doc.save(filename + 'pdf');
        }

        return (
            <>
                <form onSubmit={handleSubmit}>
                    <>
                        <div className="form-row">
                            <div className="form-group col-sm-12">
                                <div className="card-header bg-info text-white shadow rounded ">
                                    File name
                                </div>
                                <input
                                    type="text"
                                    className="form-control"
                                    id="filename"
                                    name="filename"
                                    placeholder="Enter filename without '.pdf'"
                                    value={filename}
                                    onChange={event => handleFilenameChange(event)}
                                />
                            </div>
                        </div>
                        <div className="form-row">

                            {inputFields.map((inputField, index) => (
                                <Fragment key={`${inputField}~${index}`} >
                                    {inputField.title != null ? (
                                        <div className="form-group col-sm-10 mt-4 ">
                                            <div className="card-header list-group-item-info text-black-50">
                                                Title
                                                <p style={{float: 'right'}}> Content number {index + 1}</p>
                                            </div>
                                            <input
                                                type="text"
                                                className="form-control shadow rounded"
                                                id="title"
                                                name="title"
                                                placeholder='Title...'
                                                value={inputField.title}
                                                onChange={event => handleInputChange(index, event)}
                                            />
                                        </div>) : (
                                        <div className="form-group col-sm-10 mt-4">
                                            <div className="card-header list-group-item-info text-black-50">
                                                Paragraph
                                                <p style={{float: 'right'}}>Content number {index + 1}</p>
                                            </div>
                                            <textarea className="form-control shadow rounded"
                                                      name="Text"
                                                      placeholder='Paragraph...'
                                                      rows="3"
                                                      id="title"
                                                      value={inputField.text}
                                                      onChange={event => handleInputChange(index, event)}
                                            ></textarea>

                                        </div>)}
                                    <div className=" col-sm-2 d-flex flex-column justify-content-center align-items-center ">
                                        <Button
                                            type='submit'
                                            onClick={() => handleRemoveFields(index)}
                                            className='btn-danger w-100 shadow rounded'>Remove</Button>
                                        <Button
                                            type='submit'
                                            onClick={() => paste(index)}
                                            className='btn-success  w-100 mt-2 shadow rounded'>Paste</Button>
                                    </div>

                                </Fragment>
                            ))}
                        </div>
                        <br/>
                        <div className='form-row mt-3'>
                            <div className="form-group col-sm-6 text-center">
                                <Button
                                    type='submit'
                                    onClick={() => handleAddFieldsText()}
                                    className='btn-info w-100 shadow rounded'>Add paragraph</Button>
                            </div>
                            <div className="form-group col-sm-6 text-center">
                            <Button
                                    type='submit'
                                    onClick={() => handleAddFieldsTitle()}
                                    className='btn-info w-100 shadow rounded'>Add Title</Button>
                            </div>
                        </div>
<br/>
                        <div>
                            <div className="form-group w-100 text-center">
                                <Button
                                    type='submit'
                                    onClick={() => toPdf()}
                                    className='btn-success w-100 shadow rounded'>Generate PDF</Button>
                            </div>
                            <div className='shadow rounded'>
                                <div className="card-header bg-info text-white">
                                    Preview
                                    <p style={{float: 'right'}}> This is a preview of the pdf file</p>
                                </div>
                                <div id='pdf'>
                                    {inputFields.map((inputField, index) => (
                                            inputField.title != null ? (
                                                <>
                                                    <br/><br/>
                                                <h3 className='text-break  pl-4 pr-4 '><strong>{inputField.title}</strong></h3>
                                                <br/>
                                                </>
                                            ) : (
                                                <>
                                                <p class="text-break pl-4 pr-4  "> {inputField.text}</p>
                                                    <br/>
                                                </>
                                                )
                                        )
                                    )}
                                </div>
                            </div>
                        </div>
                    </>
                </form>

            </>
        );
    }
;
export default GeneratePdf;

