import React, {Component} from "react";
import {Editor} from "./Editor/Editor";

export class OnlineCourses extends Component {

    render() {
        let user = localStorage.getItem('user');
        if (user !== null) {
            return <><Editor category='mainonlinecourses' readOnly={false}/>
            </>
        }
        else {
            return  <><Editor category='mainonlinecourses' readOnly={true}/>
            </>
        }
    }
}