import React, {Component} from "react";
import {Editor} from "./Editor/Editor";

export class LawsAndRegulations extends Component {
    constructor(props) {
        super(props);
    }

    render() {
        let user = localStorage.getItem('user');
        if (user !== null) {
            return <><Editor category='mainlawsandregulations' readOnly={false}/>
            </>
        }
        else {
            return  <><Editor category='mainlawsandregulations' readOnly={true}/>
            </>
        }
    }

}