// tools.js
import Embed from '@editorjs/embed'
import Table from '@editorjs/table'
import Paragraph from '@editorjs/paragraph'
import List from '@editorjs/list'
import Warning from '@editorjs/warning'
import LinkTool from '@editorjs/link'
import Image from '@editorjs/simple-image'
import Raw from '@editorjs/raw'
import Header from '@editorjs/header'
import Marker from "@editorjs/marker"
import CheckList from '@editorjs/checklist'
import Delimiter from '@editorjs/delimiter'
import InlineCode from '@editorjs/inline-code'
import ImageTool from '@editorjs/image'

export const EDITOR_JS_TOOLS = {
    embed: {
        class: Embed,
        config: {
            services: {
                youtube: true,
                coub: true,
                Instagram: true,
                Twitter: true
            }
        }
    },
    table: {
        class: Table,
        inlineToolbar: true
    },
    paragraph: {
        class: Paragraph,
        inlineToolbar: true
    },
    list: {
        class: List,
        inlineToolbar: true
    },
    warning: {
        class: Warning,
        inlineToolbar: true
    },
    linkTool: {
        class: LinkTool,
        config: {
            endpoint: '/fetchUrl'
        }
    },
    SimpleImage: {
        class: Image,
    },
    image: {
        class: ImageTool,
        config: {
            endpoints: {
                byFile: '/uploadImage', // Your backend file uploader endpoint
            }
        },
        inlineToolbar: true
    },
    raw: {
        class: Raw,
        inlineToolbar: true
    },
    header: {
        class: Header,
        inlineToolbar: true
    },
    marker: {
        class: Marker,
        inlineToolbar: true
    },
    checklist: {
        class: CheckList,
        inlineToolbar: true
    },
    delimiter: {
        class: Delimiter,
        inlineToolbar: true
    },
    inlineCode: {
        class: InlineCode,
        inlineToolbar: true
    }
}


