import React, {Fragment, useState} from 'react';
import Message from './Message';
import Progress from './Progress';
import axios from 'axios';
import {Link} from "react-router-dom";

const FileUpload = (props) => {
    const [file, setFile] = useState('');
    const [filename, setFilename] = useState('Choose File');
    const [name, setName] = useState();

    const [uploadedFile, setUploadedFile] = useState({});
    const [message, setMessage] = useState('');
    const [uploadPercentage, setUploadPercentage] = useState(0);

    const onChange = e => {
        const filename = e.target.files[0].name;
        if (true) {
            setFile(e.target.files[0]);
            setFilename(e.target.files[0].name);
        } else {
            setMessage('Not allowed file files are allowed');
            setFile(null);
            setFilename(null);
        }

    };
    const handleChange = e => {
        setName(e.target.value)
    };

    const reload = () => {
        window.location.reload(false);
    }
    const onSubmit = async e => {
        e.preventDefault();
        const formData = new FormData();
        formData.append('file', file);
        formData.append('title', name);
        formData.append('category', props.category);


        try {
            const res = await axios.post('/upload', formData, {
                headers: {
                    'Content-Type': 'multipart/form-data'
                },
                onUploadProgress: progressEvent => {
                    setUploadPercentage(
                        parseInt(
                            Math.round((progressEvent.loaded * 100) / progressEvent.total)
                        )
                    );

                    // Clear percentage
                    setTimeout(() => setUploadPercentage(0), 10000);
                }
            });

            const {fileName, filePath} = res.data;
            console.log(res.data.title);

            if (res.data.success) {
                setUploadedFile({fileName, filePath});
                setMessage('File Uploaded');
                window.setTimeout(reload, 1000);
            } else {
                console.log(res.data.msg);
            }
        } catch (err) {
            if (err.response.status === 500) {
                setMessage('There was a problem with the server');
            } else {
                setMessage(err.response.data.msg);
            }
        }
    };

    return (
        <Fragment>
            <Message msg='Upload a file'/>
            {message ? <Message msg={message}/> : null}
            <form onSubmit={onSubmit}>
                <div className='custom-file mb-4'>
                    <input type='file' className='custom-file-input mb-4' id='customFile' onChange={onChange}/>
                    <input value={name} placeholder='Title of pdf' type="text" name="name" onInput={handleChange}
                           className='form-control'/>
                    <label className='custom-file-label' htmlFor='customFile'>{filename}</label>
                </div>
                <br/><br/><br/>
                <Progress percentage={uploadPercentage}/>
                <br/>
                <div className='form-row'>
                <div className="form-group col-sm-12 text-center">
                <input
                    type='submit'
                    value='Upload'
                    className='btn btn-info w-100'
                />
                </div>
                <div className="form-group col-sm-12 text-center w-100">
                    <Link to={props.path} className='btn btn-success w-100'> Generate Pdf </Link>
                </div>
                </div>
            </form>

                    </Fragment>
                );
            };

            export default FileUpload;
