import React, {useState} from 'react';
import {
    Collapse,
    DropdownItem,
    DropdownMenu,
    DropdownToggle,
    Nav,
    Navbar,
    NavbarBrand,
    NavbarToggler,
    NavItem,
    NavLink,
    UncontrolledDropdown
} from 'reactstrap';

const Navigation = (props) => {
    const [isOpen, setIsOpen] = useState(false);



    const toggle = () => setIsOpen(!isOpen);

    const doLogout = async () => {
        try {
            let res = await fetch('/logout', {
                method: 'post',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                }
            });
            let result = await res.json();

            if (result && result.success) {
                //    UserStores.isLoggedIn = false;
                //UserStores.username = '';
                localStorage.removeItem('user');
                window.location.reload(false);

            }
        } catch (e) {
            console.log(e);
        }
    }
    let user = localStorage.getItem('user');
    if (user == null) {
        return (
            <div className='shadow sticky-top'>
                <Navbar color="light" light expand="xl">
                    <NavbarBrand href="/"><img src="/logo192.png" alt="Logo" className="img-fluid rounded mx-auto d-block " width='25'/></NavbarBrand>
                    <NavbarBrand href="/">Universal design</NavbarBrand>
                    <NavbarToggler onClick={toggle}/>
                    <Collapse isOpen={isOpen} navbar>
                        <Nav className="mr-auto" navbar>
                            <NavItem>
                                <NavLink href="/"><i className="fa fa-home"></i> Home</NavLink>
                            </NavItem>
                            <UncontrolledDropdown nav inNavbar>
                                <DropdownToggle nav caret>
                                    <i
                                        className="fa fa-graduation-cap"/> Learning material
                                </DropdownToggle>
                                <DropdownMenu right>
                                    <DropdownItem>
                                        <NavLink href="/accessiblekiosk">Accessible
                                            Kiosk</NavLink>
                                    </DropdownItem>
                                    <DropdownItem>
                                        <NavLink href="/accessibledocuments">Accessible
                                            Documents</NavLink>
                                    </DropdownItem>
                                    <DropdownItem>
                                        <NavLink href="/accessiblemobile">Accessible
                                            Mobile</NavLink>
                                    </DropdownItem>
                                    <DropdownItem>
                                        <NavLink href="/accessibleweb">Accessible
                                            Web</NavLink>
                                    </DropdownItem>
                                </DropdownMenu>
                            </UncontrolledDropdown>
                            <NavItem>
                                <NavLink href="/lawsandregulations"> <i
                                    className="fa fa-legal"></i> Laws and regulations</NavLink>
                            </NavItem> <NavItem>
                            <NavLink href="/onlinecourses"><i
                                className="fa fa-book"></i> Online courses</NavLink>
                        </NavItem>
                            <NavItem>
                                <NavLink href="/aboutus"><i
                                    className="fa fa-info"></i> About us</NavLink>
                            </NavItem>
                        </Nav>
                        <Nav navbar>
                            <NavItem>
                                <NavLink href="/login"><i className='fa fa-sign-in' aria-hidden="true"></i> Log
                                    in</NavLink>
                            </NavItem>
                        </Nav>
                    </Collapse>
                </Navbar>
            </div>
        );
    }
    else {
        return (       <div className='shadow sticky-top'>
            <Navbar color="light" light expand="xl" >
                <NavbarBrand href="/"><img src="/logo192.png" alt="Logo" className="img-fluid rounded mx-auto d-block " width='25'/></NavbarBrand>
                <NavbarBrand href="/">Universal design</NavbarBrand>
                <NavbarToggler onClick={toggle}/>
                <Collapse isOpen={isOpen} navbar>
                    <Nav className="mr-auto" navbar>
                        <NavItem>
                            <NavLink href="/"><i className="fa fa-home"></i> Home</NavLink>
                        </NavItem>
                        <UncontrolledDropdown nav inNavbar>
                            <DropdownToggle nav caret>
                                <i
                                    className="fa fa-graduation-cap"/> Learning material
                            </DropdownToggle>
                            <DropdownMenu right>
                                <DropdownItem>
                                    <NavLink href="/accessiblekiosk">Accessible
                                        Kiosk</NavLink>
                                </DropdownItem>
                                <DropdownItem>
                                    <NavLink href="/accessibledocuments">Accessible
                                        Documents</NavLink>
                                </DropdownItem>
                                <DropdownItem>
                                    <NavLink href="/accessiblemobile">Accessible
                                        Mobile</NavLink>
                                </DropdownItem>
                                <DropdownItem>
                                    <NavLink href="/accessibleweb">Accessible
                                        Web</NavLink>
                                </DropdownItem>
                            </DropdownMenu>
                        </UncontrolledDropdown>
                        <NavItem>
                            <NavLink href="/lawsandregulations"> <i
                                className="fa fa-legal"></i> Laws and regulations</NavLink>
                        </NavItem> <NavItem>
                        <NavLink href="/onlinecourses"><i
                            className="fa fa-book"></i> Online courses</NavLink>
                    </NavItem>
                        <NavItem>
                            <NavLink href="/aboutus"><i
                                className="fa fa-info"></i> About us</NavLink>
                        </NavItem>
                    </Nav>
                    <Nav navbar>
                        <NavItem>
                            <NavLink href="" onClick={doLogout}><i className='fa fa-sign-out' aria-hidden="true"></i> Log
                                out</NavLink>
                        </NavItem>
                    </Nav>
                </Collapse>
            </Navbar>
        </div>);
    }
}

export default Navigation;

