import React, {Component} from 'react';
import InputField from "../LoginComponents/InputField";
import UserStores from "../../stores/UserStores";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";

class LoginForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            username: '',
            password: '',
            buttonDisabled: false
        }
    }

    setInputValue(property, val) {
        val = val.trim();
        if (val.length > 12) {
            return;
        }
        this.setState({
            [property]: val
        })
    }

    resetForm() {
        this.setState({
            username: '',
            password: '',
            buttonDisabled: false
        })
    }


    async doLogin() {
        if (!this.state.username) {
            return;
        }
        if (!this.state.password) {
            return;
        }
        this.setState({
            buttonDisabled: true
        })
        try {

            let res = await fetch('/Trylogin', {
                method: 'post',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    username: this.state.username,
                    password: this.state.password
                })
            });

            let result = await res.json();
            if (result && result.success) {
                UserStores.loading = false;
                UserStores.isLoggedIn = true;
                UserStores.username = result.username;
                localStorage.setItem('user', JSON.stringify(UserStores));
                window.location.reload(false);
            } else if (result && result.success === false) {
                this.resetForm();
                alert(result.msg);
            }
        } catch (e) {
            console.log(e);
            this.resetForm();
        }
    }

    render() {

        return <div className='shadow rounded bg-light w-100 m-auto  border'>
            <Form className='w-100 p-4  my-auto  w-100'>
            <div className=' w-100 '>
            <h3 className='m-auto'>Log in</h3>
            <div className="form-group  m-auto">
                <br/>
                <label className='w-100'> Username
                <InputField
                    type='text'
                    placeholder='Username'
                    value={this.state.username ? this.state.username : ''}
                    onChange={(val) => this.setInputValue('username', val)}
                /></label>
            </div>
            <div className="form-group  m-auto">
                <br/>
                <label className='w-100'> Password
                <InputField
                    type='password'
                    placeholder='Password'
                    value={this.state.password ? this.state.password : ''}
                    onChange={(val) => this.setInputValue('password', val)}
                />
                </label>
            </div>
            <div className="form-group m-auto">
                <br/>
                <Button
                    type='submit'
                    disabled={this.state.buttonDisabled}
                    onClick={() => this.doLogin()}
                    className='btn-info  m-auto'>Login</Button>
            </div>
            </div>
        </Form>
        </div>
    }

}

export default LoginForm;