import React, {useState} from 'react';
import PropTypes from 'prop-types';

const Message = ({ msg }) => {

    const [show, setShow] = useState(true);


    const oneClick = () => {
        setShow(false);
    }
    return (
        <div className='alert alert-info alert-dismissible fade show' role='alert' style={{display: show ? 'block' : 'none' }}
        >
            {msg}
            <button
                type='button'
                className='close'
                data-dismiss='alert'
                aria-label='Close'
                onClick={oneClick}
            >
                <span aria-hidden='true'>&times;</span>
            </button>
        </div>
    );
};

Message.propTypes = {
    msg: PropTypes.string.isRequired
};

export default Message;
