import React from 'react';
import {Link} from "react-router-dom";
import Card from "react-bootstrap/Card";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";


export class PageList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            pages: [{}],
            empty: false
        }
    }

    async componentDidMount() {

        try {
            let res = await fetch('/openPage', {
                method: 'post',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    category: this.props.category,
                })
            });

            let result = await res.json();
            if (result && result.success) {
                const res = result.data;
                console.dir('Data from database : ' + res);

                for (let i = 0; i < res.length; i++) {
                    if(res[i].Data != null) {
                        let o = JSON.parse(res[i].Data);
                        res[i].Data = o.time;
                    }
                }
                console.dir('Data from database : ' + res);


                    this.setState({
                        pages: res
                    });
                console.dir(res);


            } else if (result && result.success === false) {
                console.log("Empty");
                this.setState({
                    empty: true
                });

            }

        } catch (e) {
            console.log(e);
        }


    }

 async deletePage (id) {
     let res = await fetch('/deletePage', {
         method: 'post',
         headers: {
             'Accept': 'application/json',
             'Content-Type': 'application/json'
         },
         body: JSON.stringify({
             id: id
         })
     });

     let result = await res.json();
     if(result.success && result){
         alert('Deleted from database');
         window.location.reload(false);
     }
     else {
         alert('Did not remove from database');
     }


}

    render() {
        let pathNow = window.location.pathname;
        let pathThen = pathNow +'/newPage/';

        {
            let user = localStorage.getItem('user');
            if (user !== null) {
                return (
                    <div >
                        <div className="card-header bg-info text-white  mt-2 shadow  mb-2 text-uppercase">
                            {this.props.name}
                            <Link to={pathThen}  className='text-white my-auto float-right ' data-toggle="tooltip"
                                  data-placement="top" title="Add new page">
                                <i className="fa fa-plus fa-lg  myAct" aria-hidden="true"></i>
                            </Link>
                        </div>
                        <Col className='p-0'>
                            {
                                !this.state.empty ? (
                                this.state.pages.map((obj, index) => {
                                    let path = '/' + obj.Category + '/page/' + obj.DocId + '/';
                                    let d = new Date();
                                    d.setTime(obj.Data);
                                    let formatted_date = d.getFullYear() + "-" + (d.getMonth() + 1) + "-" + d.getDate() + " " + d.getHours() + ":" + d.getMinutes() + ":" + d.getSeconds()

                                    return (
                                        <Card className='shadow  mb-2'>
                                            <Row className="no-gutters">
                                                <Col md="12" className='my-auto'>
                                                    <Card style={{ width: '100%' }}>
                                                        <Card.Body>
                                                            <Card.Title> {obj.Name}  </Card.Title>
                                                            <Card.Subtitle className="mb-2 text-muted">{obj.Category}</Card.Subtitle>
                                                            <Card.Link href={path}>View page</Card.Link>
                                                            <Card.Link href='' onClick={() => this.deletePage(obj.DocId)}>Delete</Card.Link>
                                                            <Card.Link href={path}>Edit</Card.Link>
                                                            <Card.Footer className="text-muted align-bottom " ><small>Updated  {formatted_date}</small></Card.Footer>
                                                        </Card.Body>
                                                    </Card>
                                                </Col>
                                            </Row>
                                        </Card>)
                                })):(null)
                            }

                        </Col>
                    </div>);
            }
            else {
                return (
                    <div>
                        <div className="card-header bg-info text-white mt-2 shadow rounded mb-2 text-uppercase">
                            {this.props.name}
                        </div>
                            {
                                !this.state.empty ? (
                                this.state.pages.map((obj, index) => {
                                    let path = '/' + obj.Category + '/page/' + obj.DocId + '/';
                                    let d = new Date();
                                    d.setTime(obj.Data);
                                    let formatted_date = d.getFullYear() + "-" + (d.getMonth() + 1) + "-" + d.getDate() + " " + d.getHours() + ":" + d.getMinutes() + ":" + d.getSeconds()

                                    return (
                                        <Card className='shadow rounded mb-2 list-group-item-action'>
                                            <Row className="no-gutters">
                                                <Col md="12" className='my-auto'>
                                                    <Card style={{ width: '100%' }}>
                                                        <Card.Body>
                                                            <Card.Title> {obj.Name}  </Card.Title>
                                                            <Card.Subtitle className="mb-2 text-muted"> {obj.Category}</Card.Subtitle>
                                                            <Card.Link href={path} className='stretched-link'>View page</Card.Link>
                                                            <Card.Footer className="text-muted align-bottom " ><small>Updated  {formatted_date} </small></Card.Footer>
                                                        </Card.Body>
                                                    </Card>
                                                </Col>
                                            </Row>
                                        </Card>)
                                })): (null)
                            }
                    </div>);
            }
        }


    }
}