// index.js
import EditorJs from "react-editor-js";
import React from "react";

import {EDITOR_JS_TOOLS} from "./tools";

export class EditorPages extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            data: {},
            name: '',
            DocId: '',
        }
        this.goBack = this.goBack.bind(this);
    }



    readOnly() {

        let editor = document.querySelectorAll(".codex-editor__redactor");
        for (let i = 0; i < editor.length; i++) {
            editor[i].style.padding = '0px';
        }



        let editable_elements = document.querySelectorAll("[contenteditable=true]");
        console.dir(editable_elements);
        for (let i = 0; i < editable_elements.length; i++) {
            console.log(editable_elements[i] + 'readOnly');
            editable_elements[i].removeAttribute("contenteditable");
            editable_elements[i].tabIndex = 0;
        }


        let tool = document.querySelectorAll(".ce-toolbar__content");
        for (let i = 0; i < tool.length; i++) {
            tool[i].style.display = "none"
        }

        let tool2 = document.querySelectorAll(".ce-toolbar");
        for (let i = 0; i < tool.length; i++) {
            tool2[i].style.display = "none"
            tool2[i].style.pointerEvents = "none";
        }


        let opentool = document.querySelectorAll(".ce-toolbar--opened");
        for (let i = 0; i < tool.length; i++) {
            opentool[i].style.display = "none"
        }
    }


    async componentDidMount() {
        this.readOnly();
        const {params} = this.props.match;
        try {
            let res = await fetch('/openPageById', {
                method: 'post',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    id: params.docId,
                })
            });

            let result = await res.json();
            if (result && result.success) {
                const res = result.data;
                console.dir('Data from database : '+res);
                let data = JSON.parse(res[0].Data);
                this.setState({
                    DocId: res[0].DocId
                });
                this.setState({
                    name: res[0].Name
                });
                console.log('Json data from database : ' + data);
                this.setState({
                    data: data
                });

            } else if (result && result.success === false) {
                console.log("Could not open database")

            }

        } catch (e) {
            console.log(e);
        }
    }


    changeName = (event) => {
        this.setState({
            name:  event.target.value
        })
        console.log(this.state.name);
        console.log(this.state.DocId);


    }

    onReady = () => {
        // https://editorjs.io/configuration#editor-modifications-callback
        console.log('Editor.js is ready to work!')
        if (this.props.readOnly) {
            setTimeout(this.readOnly, 1);
        }
    }

    onChange = () => {
        // https://editorjs.io/configuration#editor-modifications-callback
        console.log("Now I know that Editor's content changed!")
        if (this.props.readOnly) {
            setTimeout(this.readOnly, 1);
        }
    }
    clear = () => {
        this.editorInstance.clear();
    }

    async onSave() {
        const outputData = await this.editorInstance.save();
/////////////////////////////////// last edited p siden   av hvem ?  {JSON.parse(localStorage.getItem('user')).username}
        try {
            let res = await fetch('/saveExistingPage', {
                method: 'post',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    name: this.state.name,
                    data: JSON.stringify(outputData),
                    id: this.state.DocId
                })
            });

            let result = await res.json();
            if (result && result.success) {
                alert("Saved in database");
                console.log("Saved to database");
                this.goBack();

            } else if (result && result.success === false) {
                console.log("Did not save to database")

            }

        } catch (e) {
            console.log(e);
        }
        console.log('Article data: ', outputData)
    }

    goBack(){
        let path = window.location.pathname.split('/');
        this.props.history.push('/'+path[1]);    }

    render() {
        if (localStorage.getItem('user') == null) {
            setTimeout(this.readOnly, 1000);
        }
        return <div  className='shadow rounded bg-light'>
            {localStorage.getItem('user') == null ? (
                <div className='col-sm-12 bg-info p-0'>
                    <button onClick={this.goBack} type="button" className='btn btn-info '><i className="fa fa-arrow-left" aria-hidden="true"></i></button>
                </div>
            ) : (
                <div className='col-sm-12 bg-info p-0'>
                    <button onClick={this.goBack} type="button" className='btn btn-info '><i className="fa fa-arrow-left" aria-hidden="true"></i></button>
                    <button onClick={this.onSave.bind(this)} type="button" className='btn btn-info float-right '> Save </button>
                    <button onClick={this.clear} type="button" className='btn btn-info float-right'> Clear </button>
                    <input
                        type="text"
                        className="form-control"
                        id="pageName"
                        name="pageName"
                        placeholder="Enter page name"
                        value={this.state.name}
                        onChange={event => this.changeName(event)}
                    />
                </div>
            )
            }


            <EditorJs
                autofocus={true}
                data={this.state.data}
                enableReInitialize={true}
                onReady={this.onReady}
                onChange={this.onChange}
                instanceRef={instance => this.editorInstance = instance}
                tools={EDITOR_JS_TOOLS}>
            </EditorJs>
        </div>
    }
}


