import React, {PureComponent} from "react";
import throttle from "lodash.throttle";
import {Document, Page, pdfjs} from "react-pdf";
import Button from "react-bootstrap/Button";

pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;


class PdfViewer extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {width: null};
        this.throttledSetDivSize = throttle(this.setDivSize, 500);

    }

    componentDidMount() {
        this.setDivSize()
        window.addEventListener("resize", this.throttledSetDivSize)
    }

    componentWillUnmount() {
        window.removeEventListener("resize", this.throttledSetDivSize)
    }

    setDivSize = () => {
        this.setState({width: this.pdfWrapper.getBoundingClientRect().width})
    }


    render() {
        const {params} = this.props.match;
        let file = '/Files/' + params.id;

        return (
            <div>
                <div className='form-row bg-light '>
                    <div id="row" style={{height: "100%", width: "100%", display: "flex", overflow: "hidden"}}>
                        <div id="pdfWrapper" style={{width: "100%", height: 'auto'}}
                             ref={(ref) => this.pdfWrapper = ref}>
                            <PdfComponent wrapperDivSize={this.state.width} file={file} history={this.props.history}/>
                        </div>
                    </div>
                </div>
            </div>

        )
    }
}

class PdfComponent extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            numPages: null,
            pageNumber: 1,
        }
        this.goBack = this.goBack.bind(this);
    }

    onDocumentLoadSuccess = ({numPages}) => {
        this.setState({numPages});
    }

    changePage = offset => this.setState(prevState => ({
        pageNumber: prevState.pageNumber + offset,
    }));

    previousPage = () => this.changePage(-1);

    nextPage = () => this.changePage(1);

    goBack() {
        let path = window.location.pathname.split('/');
        this.props.history.push('/' + path[1]);
    }

    render() {
        const {pageNumber, numPages} = this.state;
        return (<div>
            <div className='form-row bg-info'>
                <div className="col-sm-12 text-white">

                    <label className='m-0'>
                        <button onClick={this.goBack} type="button" className='btn btn-info  text-white '><i
                            className="fa fa-arrow-left " aria-hidden="true"> </i></button>
                    </label>

                    <label className='m-0 mx-auto'>

                    <Button className='btn btn-info  text-white'
                            onClick={this.previousPage}
                            disabled={pageNumber <= 1}><i className="fa fa-angle-double-left"></i></Button>

                    {pageNumber} of {numPages}

                    <Button className='btn btn-info  text-white'
                            onClick={this.nextPage}
                            disabled={pageNumber >= numPages}><i className="fa fa-angle-double-right"></i></Button>
                    </label>


                    <a className='btn-info btn  text-white float-right' href={this.props.file} download>
                        <i className="fa fa-download" aria-hidden="true"></i></a>

                </div>
            </div>

            <div className='form-row bg-info'>
                <div className=" col-sm-12 text-center">
                </div>
            </div>
            <Document file={this.props.file} onLoadSuccess={this.onDocumentLoadSuccess}>
                <Page pageIndex={pageNumber} pageNumber={pageNumber} width={this.props.wrapperDivSize}/>
            </Document>
        </div>)
    }
}

export default PdfViewer;































































