import React from "react";
import Button from "react-bootstrap/Button";
import {ModalTitle} from "react-bootstrap";
import FileUpload from "./FileUpload";


export class PdfList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: true,
            empty: false,
            lists: [],
            buttonDisabled: false,
            selectedFile: null
        }
    }

    async componentDidMount() {
        try {

            let res = await fetch('/renderPdf', {
                method: 'post',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    category: this.props.category
                })
            });

            let result = await res.json();
            if (result && result.success) {
                this.setState({lists: result.files, loading: false, empty: false});
                console.log(result.files);

            } else if (result && result.success === false) {
                this.setState({
                    empty: true,
                    loading: false
                })
            }
        } catch (e) {
            console.log(e);
        }
    }

    async deletePDF(id) {
        try {

            let res = await fetch('/deleteFile', {
                method: 'post',
                headers: {
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify({
                    id: id
                })
            });

            let result = await res.json();
            if (result && result.success) {
                this.setState({
                    buttonDisabled: true
                })
                window.location.reload(false);
            } else if (result && result.success === false) {
            }
        } catch (e) {
            console.log(e);
        }

    }


    render() {

        let user = localStorage.getItem('user');
        let addPdfPath = '/' + this.props.category + '/addPdf/';
        if (user !== null) {
            console.log(this.state.lists);
            return (
                <div className="list-group">
                     {this.state.loading ? (
                                <div className='h4'>Loading...</div>
                            ) :
                            this.state.empty ? (
                                <>
                                <FileUpload category={this.props.category} path={addPdfPath}/>
                                <div className='h2'>Empty</div>
                                </>
                                )
                            : (
                                <div>
                                    <FileUpload category={this.props.category} path={addPdfPath}/>
                                    <ul className='list-group'> {this.state.lists.map((obj, index) => {
                                        let path = '/' + this.props.category  + '/pdf/' + obj.name;
                                        return (
                                            <li className='list-group-item list-group-item-action shadow rounded bg-light'
                                                key={obj.id}>
                                                <div className='row'>
                                                <div className='col-md-9'>
                                                    <div className='col-md-12 p-0'>
                                                        <a href={path} className=' text-dark list-group-item-action '> {obj.title} </a>
                                                    </div>
                                                    <div className='col-md-12 text-dark p-0 card-footer'>
                                                       <small className="text-muted ">Uploaded  {obj.date} </small>
                                                    </div>
                                                </div>
                                                <div className='col-md-3'>
                                                    <Button
                                                        type='submit'
                                                        disabled={this.state.buttonDisabled}
                                                        onClick={() => this.deletePDF(obj.id)}
                                                        className='btn-danger float-right m-auto'>Delete</Button>
                                                </div>
                                                </div>
                                            </li>
                                        );
                                    })
                                    }
                                    </ul>
                                </div>
                            )}
                </div>
            )
        } else {
            return (

                <div className="list-group">
                    {this.state.loading ? (
                            <div className='h4'>Loading...</div>
                        ) :
                        this.state.empty ? (
                                    <div className='h4'>Empty</div>
                            )
                            : (
                                <div>
                                    <ModalTitle> </ModalTitle>
                                    <ul className='list-group'> {this.state.lists.map((obj, index) => {
                                        let path = '/' + this.props.category  + '/pdf/' + obj.name;
                                        return (
                                            <li className='text-dark  list-group-item list-group-item-action'
                                                key={obj.id}>
                                                <div className='row'>
                                                    <div className='col-md-12'>
                                                        <div className='col-md-12 p-0'>
                                                            <a href={path} className=' text-dark stretched-link list-group-item-action '> {obj.title} </a>
                                                        </div>
                                                        <div className='col-md-12 text-dark p-0 card-footer'>
                                                            <a href={path} className=' text-dark stretched-link list-group-item-action '><small className="text-muted ">Uploaded  {obj.date} </small></a>
                                                        </div>
                                                    </div>
                                                </div>
                                            </li>
                                        );
                                    })
                                    }
                                    </ul>
                                </div>
                            )}
                </div>
            )
        }
    }

}

