import React, {Component} from "react";
import {Editor} from "../Editor/Editor";

export class AccessibleWeb extends Component {
    render() {
        let user = localStorage.getItem('user');
        if (user !== null) {
            return <><Editor category='mainaccessibleweb' readOnly={false}/>
            </>
        }
        else {
            return  <><Editor category='mainaccessibleweb' readOnly={true}/>
            </>
        }
    }
}